___CSS_LOADER_EXPORT___.push([module.id, `@media screen and (min-width: 1200px) {
  .hero {
    min-width: 1000px;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  }
}
@media screen and (max-width: 1000px) {
  .restaurants {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    width: 100%;
  }
  .detail {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    width: 100%;
  }
  .resto-item__header__poster {
    width: 100%;
    height: 200px;
    object-fit: cover;
  }
  .hero {
    max-width: 1000px;
  }
  .hero h1 {
    font-size: 3em;
    text-shadow: 1px 1px 3px;
    color: #dbe2ef;
    font-family: 'Londrina Solid', cursive;
  }
}
@media screen and (max-width: 755px) {
  .app-bar .app-bar__menu {
    display: flex;
    align-items: center;
  }
  .app-bar .app-bar__menu button {
    background-color: transparent;
    border: none;
    font-size: 30px;
    font-weight: bold;
    color: brown;
    padding: 10px;
    cursor: pointer;
    display: flex;
  }
  #navigationDrawer {
    position: absolute;
    top: 65px;
    left: -180px;
    width: 175px;
    height: 25%;
    border-radius: 10px;
    transition: all 0.3s;
    padding: 8px;
    background-color: #dbe2ef;
    overflow: hidden;
  }

  #navigationDrawer.open {
    left: 0;
  }

  #navigationDrawer .nav-menu {
    display: inline-block;
    text-decoration: none;
    width: 100%;
  }
  .nav-item {
    padding: 10px 10px 2px 10px;
  }
  .nav-item a {
    color: brown;
  }
  .nav-item a:hover {
    color: gray;
  }
  .nav-item a:focus {
    outline: 4px dashed rgb(255, 251, 0);
  }
  .hero {
    max-width: 755px;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  }
  .hero h1 {
    font-size: 3em;
    text-shadow: 1px 1px 3px;
    color: #dbe2ef;
    font-family: 'Londrina Solid', cursive;
  }
  .restaurants {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    width: 100%;
  }
}

@media screen and (max-width: 500px) {
  .restaurants {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    width: 100%;
  }
  .hero {
    max-width: 500px;
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
  }
  .hero h1 {
    font-size: 3em;
    text-shadow: 1px 1px 3px;
    color: #dbe2ef;
    font-family: 'Londrina Solid', cursive;
  }
}
